





























import { Component, Prop, Vue } from 'vue-property-decorator';
import { MessageItem, User } from '../interfaces';

@Component
export default class Message extends Vue {
    @Prop() message!: MessageItem;
    @Prop() isAuthor!: boolean;

    get boxColor() {
        if (this.isAuthor) {
            return '#90CAF9';
        } else {
            return '#FFFFFF';
        }
    }
}
